/**
 * NoData 空数据模块
 *      
 */

import React from 'react';
import './noData.css';

export default class NoData extends React.Component {
    static defaultProps = {
        icon: '',
        text: '',
        showFlag: true,
    };
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    render() {
        return (
            this.props.showFlag ?
                <div className="noData">
                    <img className="icon" src={this.props.icon} alt="" />
                    <p className="text">{this.props.text}</p>
                </div>
                : null
        );
    }
}

