/**
 * Btn 按钮
 *      click       事件
 *      text        按钮内容
 *      disabled    是否可以点击
 */

import React from 'react';
import './btn.css';

export default class Btn extends React.Component {
    static defaultProps = {
        click: function () { },
        text: '',
        disabled: true,
    };
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    render() {
        return (
            <span className={`btn default ${this.props.disabled ? 'active' : null}`} onClick={this.props.disabled ? this.props.click : null}>{this.props.text}</span>
        );
    }
}

