/**
 * Nav 有返回按钮的导航栏组件
 *      leftClick       导航左侧按钮点击事件
 *      title           导航的title文字
 *      rightDom        导航右侧Dom结构
 *      rightClick      导航右侧按钮点击事件
 */

import React from 'react';
import './nav.css';

export default class Nav extends React.Component {
    static defaultProps = {
        leftClick: function () { },
        title: '',
        rightClick: function () { },
        rightDom: null,
    };
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    render() {
        return (
            <div className="navContainer">
                <span className="goBtn" onClick={this.props.leftClick}>
                    <img src={require('../assets/nav_back.png')} alt="" />
                </span>
                <p className="title">{this.props.title}</p>
                <span className="rightDom" onClick={this.props.rightClick}>{this.props.rightDom}</span>
            </div>
        );
    }
}

