/**
 * 递归排序，将参数中所有的map字典排序
 */
function sort(arys) {
    //先用Object内置类的keys方法获取要排序对象的属性名，再利用Array原型上的sort方法对获取的属性名进行排序，newkey是一个数组
    var newkey = Object.keys(arys).sort();
    var newObj = {}; //创建一个新的对象，用于存放排好序的键值对
    for (var i = 0; i < newkey.length; i++) {
        //遍历newkey数组 
        if ("object" == typeof arys[newkey[i]] && !Array.isArray(arys[newkey[i]])) {
            newObj[newkey[i]] = sort(arys[newkey[i]]);
        } else {
            newObj[newkey[i]] = arys[newkey[i]];
        }
        //向新创建的对象中按照排好的顺序依次增加键值对
    }
    return newObj;
}

/**
 * 排序并转换成字符串返回
 */
function objKeySort(arys) {
    var newObj = {};
    newObj = sort(arys);
    var jsonstr = '';
    for (var key in newObj) {
        var value = '';
        if ("object" == typeof newObj[key]) value = JSON.stringify(newObj[key]); else value = newObj[key];
        jsonstr += key + '=' + value + '&';
    }
    return jsonstr; //返回排好序的新对象
}

// Post 方法
export function Post(url, data = {}, time = 10000) {

    data.key = '0Hind9iRVt0WhQwywVHqeqHiSELUW3sF';
    data.source = 'wx';
    data.timeStamp = new Date().getTime();
    let signStr = MD5(objKeySort(data) + 'key=0Hind9iRVt0WhQwywVHqeqHiSELUW3sF').toLocaleUpperCase();
    data.sign = signStr;

    let result = new Promise((resolve, reject) => {

        let requestOption = {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }

        fetch(url, requestOption)
            .then((response) => response.json())
            .then((responseData) => {
                switch (responseData.status) {
                    case 'SUCCESS':
                        resolve(responseData)
                        break;
                    case 'SYSTEM_ERROR':
                        message.error('系统异常')
                        break;
                    case 'INVALID_PARAM':
                        message.error('无效参数')
                        break;
                    case 'INVALID_PUBLIC_PARAM':
                        message.error('无效公共参数')
                        break;
                    case 'API_NOT_EXIST':
                        message.error('该方法不存在')
                        break;
                    case 'INVALID_SIGN':
                        message.error('无效签名或已过期')
                        break;
                    case 'DUPLICATE_DATA':
                        message.error('数据重复')
                        break;
                    default:
                        resolve(responseData)
                        break;
                }
            })
            .catch((error) => {
                console.log('接口请求错误信息', error);
                message.error('系统异常，请稍后重试')
            })

    })

    // 超时处理
    let timeout = new Promise(function (resolve, reject) {
        setTimeout(() => { resolve({ status: 'NONETWORK', msg: '请求超时' }) }, time)
    });

    return Promise.race([
        result, // 正常访问接口返回的数据
        timeout, // 超过请求时间时返回 "timeout"
    ]);
}

