/**
 * UpFile 上传图片方法
 *
 * 返回 图片路径数组 [imgUrl,imgUrl,...]
 */
export function UpFile(file) {
    return new Promise((resolve, reject) => {
        Get(requestUrl.getQiNiuYunToken).then((result) => {
            if (result.status === "SUCCESS") {
                let formData = new FormData();
                let _time = parseInt(new Date().getTime());
                let suiji = Math.floor(Math.random() * 1000)
                let year = new Date().getFullYear();
                let month = new Date().getMonth() + 1;
                let day = new Date().getDate();
                let path = year + '/' + month + '/' + day + '/' + _time + suiji + file.name.substr(file.name.lastIndexOf("."));
                formData.append("key", path);
                formData.append("token", result.data);
                formData.append('file', file);
                fetch("https://up-z1.qiniup.com", {
                    method: 'POST',
                    headers: {},
                    body: formData,
                    contentType: false,
                    processData: false,
                }).then((response) => response.json()).then((result) => {
                    let imgUrl = ImgUrl + result.key
                    resolve(imgUrl)
                }).catch((error) => {
                    console.log('error', error);
                });
            } else {
                message.error("上传图片失败", 2);
            }
        })


    })

}