/**
 * 地址页面 addrList
 */

import React from 'react';
import Nav from '../../components/nav'; // 导航组件
import './addrList.css';
import NoData from '../../components/noData'; // 无数据组件
import Btn from '../../components/btn'; // 按钮组件
import { Get, Post } from '../../network/Fetch';
import { requestUrl } from '../../network/Url';
import * as Public from '../../utils/Public';
import { Skeleton, message } from 'antd';
import { connect } from 'react-redux'; // 引入connect函数
import * as addrAction from '../../store/addrAction';

class addrList extends React.Component {
    static defaultProps = {

    };
    constructor(props) {
        super(props);
        this.state = {
            addrArr: [],// 收货地址数据 
            loadingFlag: false,
            type: '',
            /*
            收货地址item
            {
                address: '',//详细地址
                areaCode: '',//区id
                cityCode: '',// 市id
                createDate: '',//创建时间
                delFlag: '',//删除标识
                id: '',//地址id
                isDefault: '',//默认地址
                provinceCode: '',//省id
                provinceName: '',// 省市区
                updateDate: '',//创建时间
                userId: '',//用户id
                userName: '',// 用户姓名
                userPhone: '',//用户手机号
            }
            */
        }
    }

    componentWillMount() {
        if (this.props.match.params) {
            this.setState({
                type: this.props.match.params.type
            })
        }
    }

    componentDidMount() {
        this.findUserAddress();
    }

    render() {
        return (
            <div className="container">
                <Nav title="地址列表" leftClick={() => { this.props.history.goBack() }} />
                <div className="addrList">
                    {this.state.loadingFlag ? <Skeleton active /> : null}
                    {this.state.addrArr.length > 0 ? this.state.addrArr.map((item, index) => {
                        return (
                            <div className="addItem" key={index} onClick={() => {
                                if (this.state.type === 'select') {
                                    this.props.saveAddr(item);
                                    this.props.history.goBack();
                                }
                            }}>
                                <div className="addrBox">
                                    <p className="addInfo"><span className="name">{item.userName}</span><span className="tel">{item.userPhone}</span></p>
                                    <p className="addrDesc">{item.provinceName + item.address}</p>
                                </div>
                                <div className="btnBox">
                                    <span className="btnItem editBtn" onClick={(e) => {
                                        this.props.history.push({ pathname: '/addAddr', state: { sourceType: 'edit', addrObj: item } });
                                        e.stopPropagation()
                                    }}>编辑</span>
                                    <span className="btnItem delBtn" onClick={(e) => {
                                        this.delUserAddress(item.id);
                                        e.stopPropagation()
                                    }}>删除</span>
                                </div>
                            </div>
                        )
                    }) : <NoData showFlag={!this.state.loadingFlag && this.state.addrArr.length <= 0} text={'暂无收货地址'} />}
                </div>


                <Btn text={'+新增收货地址'} click={() => { this.props.history.push({ pathname: '/addAddr', state: { sourceType: 'add', addrObj: {} } }) }} />

            </div>
        );
    }

    // 获取地址列表
    findUserAddress() {
        this.setState({
            loadingFlag: true,
        })
        Get(requestUrl.findUserAddress + '?userId=' + Public.getItem('userId')).then((result) => {
            this.setState({
                loadingFlag: false,
            })
            if (result.status === 'SUCCESS') {
                this.setState({
                    addrArr: result.data
                })
            } else if (result.status === 'NONE_DATA') {
                this.setState({
                    addrArr: [],
                })
            } else {
                message.error('获取收到地址失败')
            }
        }).catch((error) => {
            console.log(error)
        })
    }

    // 删除收货地址
    delUserAddress(addrId) {
        Post(requestUrl.delUserAddress, {
            userId: Public.getItem('userId'),
            id: addrId
        }).then((result) => {
            if (result.status === 'SUCCESS') {
                let tempArr = this.state.addrArr;
                for (let i = 0; i < tempArr.length; i++) {
                    if (tempArr[i].id === addrId) {
                        Public.arrDelete(tempArr, i)
                    }
                }
                this.setState({
                    addrArr: tempArr,
                })
            } else {
                message.error('删除收货地址失败', 2)
            }
        }).catch((error) => {
            console.log(error)
        })
    }
}


export default connect(
    (state) => (
        { addrObj: state.addressObj, }
    ),
    (dispatch) => ({
        saveAddr: (addrObj) => dispatch(addrAction.saveAddr(addrObj)),
    })
)(addrList)